.list-thumb {
  margin-bottom: 15px;
}


.list-thumb div {
  color: #000000;
}

.list-thumb .title {
  height: 45px;
  font-weight: bold;
}

.list-thumb .info {
  font-size: 90%;
}

.list-thumb .img-wrap {
  width: 20vw;
  height:20vw;
  display: table-cell;
  vertical-align: middle;
}

.panel-heading {
  font-weight: bold;
  font-size: 110%;
  text-transform: capitalize;
}
